@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{--sidebar-flex: 1.5;--condiv-flex: 6;--fontsize-p: 1.2rem;--fontsize-nav: 1.6rem;--typing-font: 3rem}*{margin:0;padding:0}body{font-family:"Raleway"}.App{display:flex;width:100vw;min-height:100vh}nav{padding:60px 30px 0px 30px;flex:1.5 1;flex:var(--sidebar-flex) 1}.navbar{background:linear-gradient(180deg, #aa4b6b, #6b6b83, #3b8d99)}.condiv{padding:100px;background-color:#f4f5f9;flex:6 1;flex:var(--condiv-flex) 1}.home{display:flex;justify-content:center;flex-direction:column;align-items:center}.profilepic{border-radius:50%;width:200px;margin:40px}.typingeffect{font-size:3rem;font-size:var(--typing-font);margin-bottom:30px}nav ul{font-size:1.6rem;font-size:var(--fontsize-nav);text-align:center;list-style-type:none;text-decoration:none;color:white !important}nav ul li{margin:40px}.skillsList{-webkit-column-count:2;column-count:2}a{text-decoration:none;color:white}.subtopic{margin:10px 10px 30px 10px !important}p{font-size:1.2rem;font-size:var(--fontsize-p)}h1,h2,h3,h4,p{margin:10px}.social{position:absolute;margin-top:20px;display:flex;bottom:60px}.social i{padding-right:30px;font-size:25px;color:black}.social i:hover{color:black}.widecard{border:0px solid #9a9b9c;margin:30px 0px 30px 0px}.widecardskills{border:0px solid #9a9b9c;border-radius:10%;width:100px;height:100px;display:flex;position:relative}.widecard:hover{color:#1b1d20;background-color:#ffffff;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23)}.widecardskill:hover{color:#1b1d20;background-color:#ffffff;box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23)}.active{font-weight:bolder}.skills ul li{list-style-type:none;margin:30px}.box-container{display:flex}.box{width:50%;height:50%}.row:after{content:"";display:table;clear:both;width:33.3%}.column{float:left;width:33.33%}.card{width:280px;height:500px;margin:60px;box-shadow:0px 0px 0px 0px #eb507e;transition:0.25s}.card:hover{cursor:pointer}.card:hover .button-primary{transform:translate(10px, 0)}.card-header{height:150px;width:100%;padding:15px;background-size:cover;color:#fff}.card-header--title{text-transform:uppercase;margin:0}.card-body{padding:15px;background-color:#fff;width:100%}.date{font-size:11px;font-weight:600;color:grey}.body-content{font-size:13px;line-height:1.8}.button-primary{border:none;box-shadow:none;font-family:inherit;background-color:transparent;color:#341cac;font-size:15px;transition:0.25s}.button-primary i{padding-right:4px}@media all and (max-width: 450px){:root{--fontsize-nav: 1rem;--sidebar-flex: 0;--typing-font: 2rem}.App{flex-direction:column}.sidebar{padding:30px;background:#ee678f;background:linear-gradient(90deg, #ee678f 50%, #847cfc 80%)}.condiv{padding:50px}nav ul{display:flex}nav ul li{margin:10px}.social{left:0;width:100vw;display:flex;justify-content:space-around;bottom:30px}.social i{padding-right:0px}.column{width:100%}}

